import React, { Component } from 'react';
import '../style/blogDisplay.css';
import { Request } from '../config/utils';
import BlogComment from './blogComment';

export default class blogDisplay extends Component {
    constructor(props) {
        super(props);
        this.state = {
            id: props.location.query.id,
            title: "",
            date: "",
            writer: "",
            content: "",
            error: "",
            /* 默认显示评论 */
            notComment: props.notComment || false,
            notPublish: props.notPublish || false,
        }
    }

    /* 加载文章 */
    componentWillMount() {
        Request.getArticleContent(this.props.location.query.id).then(
            res => {
                this.setState(res.data);
                console.log(this.state)
            }
        ).catch(error=>this.setState(error));
    }

    render() {
        if(this.state.error){
            return <h2>{this.state.error.massage}</h2>
        }
        else{
            return (
            <div id="blog">
                <h1 className="blogtitle">{this.state.title}</h1>
                <p className="blogwriter">{this.state.writer}</p>
                <p className="blogdate">{this.state.date}</p>
                <div id="blogtext">
                    <pre>{this.state.content}</pre>
                </div>
                {this.state.notComment?null:<BlogComment articleID={this.state.id} notPublish={this.state.notPublish}/>}
            </div>
            );
        }
        
    }
}