import React, { Component } from "react";
import { Menu, Icon } from "antd";
import { Url, StatusCode } from '../config/config';
import { Request } from '../config/utils';
import '../style/blogFrame.css';

const SubMenu = Menu.SubMenu;

export default class BlogMenu extends Component {
    constructor() {
        super();
        this.state = {
            isLogin: false,
            userName: "",
        }
        this.handleLogoutClick = this.handleLogoutClick.bind(this);
    }

    handleLogoutClick() {
        Request.getLogout().then(
            res => {
                console.log(res);
                if(res.data.statusCode === StatusCode.OK){
                    this.setState({
                        isLogin:false,
                        userName:"",
                    });
                    window.alert("退出成功!");
                } else {
                    window.alert("退出失败!");
                }
            }
        ).catch(
            error => {
                console.log(error);
            }
        )
    }

    componentDidMount() {
        Request.getLoginUser().then(
            res => {
                console.log("res:" ,res.data);
                if(res.data.statusCode === StatusCode.OK){
                    this.setState({
                        userName: res.data.user_username,
                        isLogin: true,
                    });
                }
            }
        ).catch(
            error => {
                console.log(error);
            }
        )
    }

    render() {
        const personalCenter = 
            <SubMenu 
                key="sub1"
                style={{float:"right"}}
                title={<span className="submenu-title-wrapper"><Icon type="user" /><span>Hello! {this.state.userName}</span></span>}>
                <Menu.Item key={Url.blogManagementPage}>
                    <Icon type="database" />我的博文
                </Menu.Item>
                <Menu.Item key="modify" >
                    <Icon type="setting" />信息修改
                </Menu.Item>
                <Menu.Item key="logout" onClick={this.handleLogoutClick}>
                    <Icon type="logout" />退出登陆
                </Menu.Item>
            </SubMenu>;
        const loginBlock = <Menu.Item key={Url.loginPage} style={{float:"right"}}>登录</Menu.Item>
        return (
            <Menu className="Menu"
              theme="dark"
              mode="horizontal"
              defaultSelectedKeys={["1"]}
              style={{ lineHeight: "64px" }}
              onClick={this.props.handleClick}
            >
              <Menu.Item key={Url.homePage}>首页</Menu.Item>
              <Menu.Item key={Url.introductionPage}>简介</Menu.Item>
              <Menu.Item key={Url.blogListPage}>帖子</Menu.Item>
              { this.state.isLogin ? personalCenter : loginBlock }
            </Menu>
        );
    }
}