import React, { Component } from "react";
import { Layout } from "antd";
import '../style/blogFrame.css';
import BlogMenu from './blogMenu';

const { Header, Footer } = Layout;

export default class BlogFrame extends Component {
  handleClick = (e) => {
    console.log(e.key);
    this.props.router.push(e.key);
  }

  render() {
    return (
      <div>
        <Layout>
          <Header>
            <div className="logo">
              <img src={require("../images/logo.png")} alt="logo"  height="60px" />
            </div>
            <BlogMenu handleClick={this.handleClick}/>
          </Header>
          <Layout style={{minHeight:780}}>
            {this.props.children}
          </Layout>
          {/*footer */}
          <Footer className="footer">
            © YINGYAN-Team
          </Footer>
        </Layout>
      </div>
    );
  }
}
