import { api, Url, StatusCode } from './config';
import axios from 'axios';
import { browserHistory } from 'react-router';

/* axios请求 */
const postLogin = (body) =>
    axios.post(api.Login, body)

const getLoginUser = () => 
    axios.get(api.getLoginUser)

const deleteBlog = (body) =>
    axios.post(api.blog_article_delete, body)

const getArticleContent = (id) =>
    axios.get(api.blog_article_read + "?id=" + id)

const postReviseBlog = (body) =>
    axios.post(api.blog_article_modify, body)

const postNewBlog = (body) =>
    axios.post(api.blog_article_write, body)

const postRegister = (body) => 
    axios.post(api.Register, body)

const getCurrentListPage = (body) =>
    axios.post(api.blog_list, body)

const getCurrentBlogCommentPage = (id, body) =>
    axios.post(api.blog_article_comment_list+"/"+id, body)

const postBlogComment = (id, body) =>
    axios.post(api.blog_article_comment_write+"/"+id, body)

const getCurrentUserListPage = (body) =>
    axios.post(api.user_management_list, body)

const deleteUser = (id) => 
    axios.post(api.user_management_delete, id)

const showUserBlog = (id, body) =>
    axios.post(api.user_management_showblog + "/" + id, body)

const postReviseUser = (body) =>
    axios.post(api.user_management_modify, body)

const getLogout = () => 
    axios.get(api.Logout)

/* 管理员每次请求都要调用该函数以检验身份 */
let judgeRes = function(res) {
    console.log(res);
    if (res.error) {
        window.alert(res.error);
    }
    //测试用例
    let { statusCode }  = res.data;
    if(statusCode === StatusCode.OK){
        console.log("res OK");
        return true;
    } else if(statusCode === StatusCode.NOT_LOGIN){
        window.alert("You haven't logined yet!");
        browserHistory.push(Url.loginPage);
        return false;
    }
    return false;
}

/* 时间格式化 */
function getNowFormatDate(day) 
{ 
    var Year = 0;
    var Month = 0;
    var Day = 0;
    var Hour = 0;
    var Minute = 0;
    var Second = 0;
    var CurrentDate = "";
    //初始化时间 
    //Year= day.getYear();//有火狐下2008年显示108的bug 
    Year= day.getFullYear();//ie火狐下都可以 
    Month= day.getMonth()+1; 
    Day = day.getDate(); 
    Hour = day.getHours(); 
    Minute = day.getMinutes(); 
    Second = day.getSeconds(); 
    CurrentDate += Year + "-"; 
    if (Month >= 10 ) { 
        CurrentDate += Month + "-"; 
    } else { 
        CurrentDate += "0" + Month + "-"; 
    } 
    if (Day >= 10 ) { 
        CurrentDate += Day + " "; 
    } else { 
        CurrentDate += "0" + Day + " ";
    } 
    if (Hour >= 10 ) { 
        CurrentDate += Hour + ":"; 
    } else { 
        CurrentDate += "0" + Hour + ":"; 
    }
    if (Minute >= 10 ) { 
        CurrentDate += Minute + ":"; 
    } else { 
        CurrentDate += "0" + Minute + ":"; 
    }
    if (Second >= 10 ) { 
        CurrentDate += Second; 
    } else { 
        CurrentDate += "0" + Second;
    }
    return CurrentDate; 
}

/* 获取div元素在页面中的绝对高度 */
function getOffsetTop(el){
    return el.offsetParent
     ? el.offsetTop + getOffsetTop(el.offsetParent)
     : el.offsetTop
}

const Request = {
    postLogin: postLogin,
    getLogout: getLogout,
    getLoginUser: getLoginUser,
    deleteBlog: deleteBlog,
    getArticleContent: getArticleContent,
    postReviseBlog: postReviseBlog,
    postNewBlog: postNewBlog,
    postRegister: postRegister,
    judgeRes: judgeRes,
    getCurrentListPage: getCurrentListPage,
    getCurrentBlogCommentPage: getCurrentBlogCommentPage,
    postBlogComment: postBlogComment,
    getCurrentUserListPage: getCurrentUserListPage,
    deleteUser: deleteUser,
    showUserBlog: showUserBlog,
    postReviseUser: postReviseUser,
};

export {
    Request,
    getNowFormatDate,
    getOffsetTop,
}