import React, { Component } from "react";
import { List, Avatar } from "antd";
import "../style/blogFrameSiderList.css";
import { Request } from '../config/utils';


/*修改Content内容*/
function setContent(newText, newAuthor){
    document.getElementById('contentText').innerText = newText;
    document.getElementById('author').innerText = newAuthor;
}

function sortBlogByTime(articles){  //按时间顺序将博客排序,应该读取数据时已经按时间顺序排序好了？？

}

export default class SiderList extends Component {
  constructor() {
      super();
      this.state = {
        pageSize: 5,
        articles: []
      };
  }

  componentDidMount() {
    const body = {
      current: 1,
      pagesize: this.state.pageSize,
  }
    Request.getCurrentListPage(body).then(
      res => {
      this.setState({
        articles: res.data.articles,
      });
      console.log(this.state);
    });
  }

  render() {
    return (
      <List className="list"
      itemLayout="horizontal"
      dataSource={this.state.articles}
      renderItem={item => (
        <List.Item>
          <List.Item.Meta 
            avatar={<Avatar src="https://zos.alipayobjects.com/rmsportal/ODTLcjxAfvqbxHnVXCYX.png" />}
            title={
              <a onClick={() => setContent(
                item.text,
                item.writer+'/'+item.date
                )} >
                {item.writer} / {item.date}
              </a>
            }   //存放作者和创作时间
            description={item.title}    //存放文章标题
          />
        </List.Item>
      )}
    />
    );
  }
}
