import React, { Component } from 'react'
import { Input } from 'antd/lib';
import { Button, Form } from 'antd';
import 'antd/dist/antd.css';
import { Request } from '../../config/utils';
import { Url, StatusCode } from '../../config/config';
import Error from '../ErrorPage';

export default class AddComment extends Component {
    constructor(props){
        super(props);
        this.state = {
            id: props.articleID,
            writer: "",
            content: "",
        }
        this.handleChange = this.handleChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
    }

    handleSubmit(event) {
        event.preventDefault();
        console.log(this.state);
        const comment = {
            body: this.state.content
        }
        console.log("body:",comment);
        Request.postBlogComment(this.state.id, comment).then(
            res => {
                if(Request.judgeRes(res) === true){
                    window.alert("add comment success!");
                    /* 重新请求评论数据 */
                    this.props.onBlogCommentSubmit(1);
                    /* 清空写评论区域 */
                    this.setState({
                        content:"",
                    })
                }
            }
        );
    }

    handleChange(event) {
        this.setState({
            [event.target.name]: event.target.value
        });
    }

    render() {
        const { TextArea } = Input;
        return (
            <Form onSubmit={this.handleSubmit} className="edit-Form">
                写评论
                <TextArea name="content" className="edit-TextArea" rows={30} value={this.state.content} onChange={this.handleChange} required />
                <Button type="primary" htmlType="submit" className="edit-Button" >
                    Add Comment
                </Button>
            </Form>
        )
    }
}