import React, { Component } from 'react';
import { Request } from '../../config/utils';
import { Table, Popconfirm, Button } from 'antd';
import { Url } from '../../config/config';
import Error from '../ErrorPage';

function onChange(pagination, filters, sorter) {
    console.log('params', pagination, filters, sorter);
}

export default class UserManagement extends Component {
    constructor() {
        super();
        this.state = {
            users: [],
            total: 0,
            current: 1,
            pageSize: 10,
            isLoaded: false,
            error: null,
        }

        this.columns = [{
            title: 'ID',
            dataIndex: 'id',
            defaultSortOrder: 'descend',
            sorter: (a, b) => a.id - b.id,
        }, {
            title: 'UserName',
            dataIndex: 'name',
            sorter: (a, b) => a.name.localeCompare(b.name),
        }, {
            title: 'RegisterDate',
            dataIndex: 'registerdate',
            sorter: (a, b) => a.registerdate.localeCompare(b.registerdate),
        }, {
            title: 'PostNum',
            dataIndex: 'postnum',
            sorter: (a, b) => a.post - b.post,
        }, {
            title: 'Email',
            dataIndex: 'email',
        }, {
            title: 'Action',
            dataIndex: 'action',
            render: (text, record) => (
                this.state.users.length >= 1 ? (
                    <div>
                        <Button onClick={() => this.showUserBlog(record)}>Show</Button>
                        <Button onClick={() => this.handleUserRevise(record)}>Revise</Button>
                        <Popconfirm title="Sure to delete?" onConfirm={() => this.handleDelete(record.id)}>
                                <Button type="danger">Delete</Button>
                        </Popconfirm>
                    </div>
                ) : null
            )
        }];

        this.gotoThispage = this.gotoThispage.bind(this);
        this.handleDelete = this.handleDelete.bind(this);
        //this.handleUserRevise = this.handleUserRevise.bind(this);
        this.showUserBlog = this.showUserBlog.bind(this);
    }

    /* 页面加载获取第一页列表 */
    componentDidMount() {
        this.gotoThispage(this.state.current);
    }

    /* 获取指定页码的列表 */
    gotoThispage(current) {
        const body = {
            current: current,
            pagesize: this.state.pageSize,
        }
        Request.getCurrentUserListPage(body).then(
            res => {
                console.log("res:", res.data);
                if(Request.judgeRes(res) === true){
                    this.setState({
                        current: current,
                        users: res.data.users,
                        total: res.data.total,
                        isLoaded: true,
                    });
                }
            }
        ).catch(
            error => this.setState({
                isLoaded: true,
                error
            })
        )
    }

    /* 列出用户所有博文 */
    showUserBlog(key) {
        this.props.router.push({
            pathname: Url.adminblogManagementPage,
            query:{
                id: key.id,
            }
        })
        /*Request.showUserBlog(key.id).then(
            res => {
                if(Request.judgeRes(res) === true){
                    this.props.router.push({
                        pathname: Url.blogManagementPage,
                        query: {
                            Id: key,
                        }
                    });
                }
            }
        ).catch(
            error => this.setState({
                isLoaded: true,
                error
            })
        )*/
    }

    /* 删除用户 */
    handleDelete(key) {
        Request.deleteUser({id: key.id}).then(
            res => {
                if(Request.judgeRes(res) === true){
                    window.alert("delete success");
                    this.gotoThispage(this.state.current);
                }
            }
        ).catch(
            error => this.setState({
                isLoaded: true,
                error
            })
        )
    }

    render(){
        const self = this;
        const { error, isLoaded } = this.state;
        if (error) {
            return <Error component={error} />
        }
        else if (!isLoaded) {
            return <div>Loading...</div>
        }
        else{
            return (
                <Table
                    columns={this.columns}
                    dataSource={this.state.users}
                    rowKey={users => users.id}
                    onChange={onChange}
                    pagination={{  //分页
                        total: this.state.total, //数据总数量
                        pageSize: this.state.pageSize,  //显示几条一页
                        onChange(current) {  //点击改变页数的选项时调用函数，current:将要跳转的页数
                            self.gotoThispage(current);
                        },
                        showTotal: function () {  //设置显示一共几条数据
                            return '共 ' + self.state.total + ' 个用户';
                        }
                    }}
                />
            );
        }
    }
}