import React from 'react';
import { Layout, Menu ,Icon, List, Avatar,Row, Col,Table,Tag } from "antd";
import "../style/introduction.css";
import '../style/techIntro.css';
import 'antd/dist/antd.css';

const { Content, Sider } = Layout

class MemberIntro extends React.Component {
    constructor(){
        super();
        this.columns = [{
            title:'角色',
            dataIndex:'character',
            key:'character'
            },{
            title:'年级',
            dataIndex:'tags',
            key:'tags',
            render: tags => (
                <span>
                  {tags.map(tag => <Tag color="blue" key={tag}>{tag}</Tag>)}
                </span>
              ),
            },{
            title:'人数',
            dataIndex:'number',
            key:'number',
        }];

        this.data = [{
            key:'1',
            character:'领导者',
            tags:['大四'],
            number:'2'
            },{
            key:'2',
            character:'主力军',
            tags:['大三'],
            number:'3'
            },{
            key:'3',
            character:'新锐',
            tags:['大二'],
            number:'5'
        }];
    }
    
    render(){
        return(
            <div id="container" style={{padding: '24px'}}>
                <Table columns={this.columns} dataSource={this.data} />
            </div>
        )
    }
}

class TechIntro extends React.Component{
    render(){
        return(
            <div className="techWrap">
                <h2>技术方向:</h2>
                <Row>
                    <Col span={12}>前端:</Col>
                    <Col span={12}>后台:</Col>
                </Row>
                <Row>
                    <Col span={12}>React</Col>
                    <Col span={12}>django</Col>
                </Row>
                <Row>
                    <Col span={12}>Antd</Col>
                    <Col span={12}>Flask</Col>
                </Row>
            </div>
        )
    }
}

class HopeIntro extends React.Component{
    constructor(){
        super();
        this.data=[
            {
                title:'1'
            },
            {
                title:'2'
            },
            {
                title:'3'
            },
            {
                title:'4'
            },
        
        ];
    }
    render(){
        return(
            <div style={{color:"black"}}>
                <h1>新年祝福：</h1>
                <List
                    itemLayout="horizontal"
                    dataSource={this.data}
                    renderItem={item =>(
                        <List.Item>
                            <List.Item.Meta
                                avatar={<Avatar src="https://zos.alipayobjects.com/rmsportal/ODTLcjxAfvqbxHnVXCYX.png" />}
                                // title={<a></a>}
                                title={item.title}
                                description="and so on"
                            />
                        </List.Item>
                    )}
                />
            </div>
        )
    }
}

export default class Introduction extends React.Component {
    constructor(){
        super();
        this.state={
            page:'1',
            Collapsed:false,
            // 1:人员介绍界面,2:技术介绍界面,3:新年愿望界面
        }
        this.renderPage=this.renderPage.bind(this);
    }

    handleClick = (event) => {
        this.setState({
            page:event.key,
        })
    }
    renderPage(){
        console.log(this.state.page);
        switch(this.state.page){
            case '1':return (<MemberIntro/>);
            case '2':return (<TechIntro/>);
            case '3':return (<HopeIntro/>);
            default:return null;
        }
    }
    render() {
        return (
            <Layout>
                <Sider>
                    <Menu
                        defaultSelectedKeys={['1']}
                        defaultOpenKeys={['sub1']}
                        mode="inline"
                        theme="dark"
                        inlineCollapsed={this.state.collapsed}
                        style={{width:200}}
                        onClick={this.handleClick}
                    >
                        <Menu.Item key="1">
                            <Icon type="pie-chart" />
                            <span>人员介绍</span>
                        </Menu.Item>
                        <Menu.Item key="2">
                            <Icon type="desktop" />
                            <span>技术方向</span>
                        </Menu.Item>
                        <Menu.Item key="3">
                            <Icon type="inbox" />
                            <span>新年愿望</span>
                        </Menu.Item>
                    </Menu>
                </Sider>
                <Content>
                    {/* {this.props.children} */}
                    {this.renderPage()}
                    {/* {(()=>{
                        switch(this.state.page){
                            case '1':return (<MemberIntro/>);break;
                            case '2':return (<TechIntro/>);break;
                            case '3':return (<HopeIntro/>);break;
                            default:return null;
                        }
                    })()} */}
                </Content>
            </Layout>
                )
            }
}