import React, { Component } from 'react'
import { getNowFormatDate, Request, getOffsetTop } from '../config/utils';
import AddComment from './user/addComment';
import { List } from 'antd';

class Comment extends Component {
    render(){
        const {writer, date, content} = this.props;
        return (
        <div className="comment">
          <small className='writer_date'>
            <strong>
              <a href="#" style={{ color: "#e95420" }}>
                {writer}
              </a>
            </strong>
            <span style={{display:'inline-block'}}>
              <span>{date}</span>
            </span>
          </small>
          <div className="content">
            <p>{content}</p>
          </div>
        </div>
        ) 
    }
}

export default class BlogComment extends Component {
    constructor(props){
        super(props);
        this.state = {
            id: props.articleID,
            total: 0,
            pageSize: 5,
            current: 1,
            comments: [],
            error: null,
            notPublish: props.notPublish || false,
        }
        this.gotoThispage = this.gotoThispage.bind(this);
    }

    componentDidMount() {
        this.gotoThispage(this.state.current);
    }

    gotoThispage(current) {
        const body = {
            pagesize: this.state.pageSize,
            current: current,
        }
        Request.getCurrentBlogCommentPage(this.state.id, body).then(
            res => {
                console.log("res:", res.data);
                this.setState({
                    current: current,
                    comments: res.data.comment,
                    total: res.data.total,
                });
                window.scrollTo(0, getOffsetTop(document.getElementById("CommentTop")));
            }
        ).catch(
            error => this.setState(error)
        )
    }

    render(){
        const self = this;
        const { error, comments } = this.state;
        if(error){
            return <div><p>评论加载失败/(ㄒoㄒ)/~~</p></div>
        }
        return (
            <div>
                <div id="CommentTop"></div>
                <List
                    className="comment-list"
                    header="评论区"
                    itemLayout="horizontal"
                    bordered={true}
                    dataSource={comments}
                    locale={{emptyText:"现在还没有评论呢，来发表一下评论吧！"}}
                    renderItem={item => (
                        <List.Item key={item.id}>
                            <Comment
                                date={item.date}
                                writer={item.writer}
                                content={item.body}
                            />
                        </List.Item>)
                    }
                    pagination={{  //分页
                        total: this.state.total, //数据总数量
                        pageSize: this.state.pageSize,  //显示几条一页
                        onChange(current) {  //点击改变页数的选项时调用函数，current:将要跳转的页数
                            self.gotoThispage(current);
                        },
                        showTotal: function () {  //设置显示一共几条数据
                            return '共 ' + self.state.total + ' 条评论';
                        }
                    }}
                />
                {this.state.notPublish?null:<AddComment articleID={this.state.id} onBlogCommentSubmit={this.gotoThispage} />}
            </div>
        )
    }
}