import React from "react";
import ReactDOM from "react-dom";
import { Url } from "./config/config";
import blogFrame from "./components/blogFrame";
import adminFrame from "./components/admin/adminFrame";
import { WrappedNormalLoginForm } from "./components/login";
import blogList from "./components/blogList";
import blogDisplay from "./components/blogDisplay";
import blogManagement from "./components/user/blogManagement";
import blogRevise from "./components/user/blogRevise";
import Introduction from "./components/introduction";
import homeContent from "./components/homeContent";
import blogAdd from "./components/user/blogAdd";
import userManagement from './components/admin/userManagement';
import { WrappedRegistrationForm } from "./components/Register";
import { Router, Route, browserHistory, IndexRoute } from "react-router";

ReactDOM.render(
  <Router history={browserHistory}>
    <Route path={Url.homePage} component={blogFrame}>
      <IndexRoute component={homeContent} />
      <Route path={Url.introductionPage} component={Introduction} />
      <Route path={Url.blogListPage} component={blogList} />
      <Route path={Url.blogDisplayPage} component={blogDisplay} />

      <Route path={Url.blogManagementPage} component={blogManagement} />
      <Route path={Url.blogAddPage} component={blogAdd} />
      <Route path={Url.blogRevisePage} component={blogRevise} />
    </Route>

    <Route path={Url.loginPage} component={WrappedNormalLoginForm} />
    <Route path={Url.registerPage} component={WrappedRegistrationForm} />

    <Route path={Url.adminHomePage} component={adminFrame}>
      <IndexRoute component={userManagement} />
      <Route path={Url.adminblogAddPage} component={blogAdd} />
      <Route path={Url.adminblogManagementPage} component={blogManagement} />
      <Route path={Url.adminblogDisplayPage} component={blogDisplay}/>
      <Route path={Url.adminblogRevisePage} component={blogRevise} />
    </Route>
  </Router>,
  document.getElementById("root")
);
